<template>
  <aside class="filter">
    <h2 class="filter__title"></h2>

    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="min-price"
            v-model.number="currentPriceFrom"
          />
          <span class="form__value"></span>
        </label>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="max-price"
            v-model.number="currentPriceTo"
          />
          <span class="form__value"></span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <label class="form__label form__label--select">
          <select
            class="form__select"
            type="text"
            name="category"
            v-model.number="currentCategoryId"
          >
            <option value="0"></option>
            <option v-for="c in categories" :key="c.id" :value="c.id">
              {{ c.title }}
            </option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <ul class="colors">
          <li class="colors__item" v-for="c in colors" :key="c.id">
            <label class="colors__label">
              <input
                class="colors__radio sr-only"
                type="radio"
                name="color"
                :value="c.id" 
                v-model="currentColorId"               
              />
              <span class="colors__value" :style="'background-color: '+c.code">
              </span>
            </label>
          </li>         
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">  </legend>
        <ul class="check-list">
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="8"
                checked=""
              />
              <span class="check-list__desc">
                8
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="16"
              />
              <span class="check-list__desc">
                16
                <span>(461)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="32"
              />
              <span class="check-list__desc">
                32
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="64"
              />
              <span class="check-list__desc">
                64
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="128"
              />
              <span class="check-list__desc">
                128
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="264"
              />
              <span class="check-list__desc">
                264
                <span>(313)</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        
      </button>
      <button class="filter__reset button button--second" type="button" @click.prevent="reset">
        
      </button>
    </form>
  </aside>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config.js'

export default {
  props: ["priceFrom", "priceTo", "categoryId","colorId"],
  data(){
    return {
      currentPriceFrom:0,
      currentPriceTo:0,
      currentCategoryId:0,
      currentColorId:0,
      categoriesDate: null,   
      colorsData: null,
    }
  },
  computed: {
    categories() {
      return this.categoriesDate ? this.categoriesDate.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    }
  },
  watch:{
      priceFrom(value){
        this.currentPriceFrom=value;
      },
      priceTo(value){
        this.currentPriceTo=value;
      },
      categoryId(value){
        console.log('w cat')
        this.currentCategoryId=value;
      },
      colorId(value){
        console.log('w color')
        this.colorId=value;
      },
  },
  created(){
    this.loadCategories();
    this.loadColors();
  },
  methods: {
    loadCategories(){
      axios
        .get(API_BASE_URL + 'api/productCategories').then(response => this.categoriesDate = response.data);      
    },
    loadColors(){
      axios
        .get(API_BASE_URL + 'api/colors').then(response => this.colorsData = response.data);      
    },
    submit(){
      this.$emit('update:priceFrom',this.currentPriceFrom);
      this.$emit('update:priceTo',this.currentPriceTo);
      this.$emit('update:categoryId',this.currentCategoryId);      
      this.$emit('update:colorId',this.currentColorId);
    },
    reset(){
      this.$emit('update:priceFrom',0);
      this.$emit('update:priceTo',0);
      this.$emit('update:categoryId',0);
      this.$emit('update:colorId',0);
    },
  }
};
</script>
