<template>
  <main class="content container">
    <PreloaderBig v-if="cartLoadingState" />
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">  </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">  </a>
        </li>
      </ul>

      <h1 class="content__title"></h1>
      <span class="content__info">
        : {{ $store.state.cartProducts.length }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem
              v-for="item in cartProducts"
              :key="item.id"
              :item="item"
            />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            &nbsp;   &nbsp; 
          </p>
          <p class="cart__price">
            : <span>{{ totalPrice | numberFormat }} </span>
          </p>

          <router-link class="cart__button button button--primery" type="submit" :to="{name:'order'}" tag="button" v-show="cartProducts.length > 0">
             
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import { mapGetters } from "vuex";
import CartItem from "@/components/CartItem.vue";
import PreloaderBig from "@/components/PreloaderBig.vue";

export default {
  components: { CartItem, PreloaderBig },
  filters: { numberFormat },
  computed: {
    ...mapGetters({
      totalPrice: "cartTotalPrice",
      cartProducts: "cartProducts",
      cartLoadingState: "cartLoadingState",
    }),
  },
};
</script>